.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5rem;
}
@media (max-width: 480px) {
  .header {
    flex-direction: column;
  }
}
.header__logo {
  width: 30rem;
}

.navegacion {
  display: flex;
  gap: 2rem;
}
@media (max-width: 480px) {
  .navegacion {
    margin-top: 5rem;
    flex-direction: column;
    text-align: center;
    width: 100%;
  }
}
.navegacion__link {
  color: #000;
  font-weight: 900;
  padding: 1rem 2rem;
}
.navegacion__link--registrar {
  background-color: #e8505b;
  color: #FFFFFF;
  border-radius: 1rem;
}

.formulario__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
@media (max-width: 480px) {
  .formulario__grid {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}
@media (max-width: 480px) {
  .formulario__imagen {
    width: 20rem;
  }
}
.formulario__contenido {
  padding: 4rem;
  background-color: #e8505b;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.formulario__heading {
  color: #FFFFFF;
  text-align: center;
}
.formulario__buscar {
  display: flex;
  flex-direction: column;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
}
.formulario__label {
  color: #FFFFFF;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.formulario__input {
  margin-bottom: 1rem;
  padding: 1rem;
  border: 0;
}
.formulario__submit {
  padding: 1rem;
  background-color: #455a64;
  border: 0;
  border-radius: 1rem;
  margin-top: 1rem;
  color: #FFFFFF;
}
.formulario__submit:hover {
  cursor: pointer;
}

.pasos {
  margin-top: 10rem;
}
.pasos__contenido {
  background-color: #e8505b;
}
.pasos__elemento {
  transition-property: transform;
  transition-duration: 300ms;
}
.pasos__elemento:hover {
  transform: rotate(-4deg) scale(1.2);
}
.pasos__heading {
  color: #FFFFFF;
  text-align: center;
}
.pasos__heading span {
  display: block;
  font-size: 1.6rem;
}
.pasos__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 480px) {
  .pasos__grid {
    grid-template-columns: 1fr;
  }
}

.testimoniales {
  margin-top: 5rem;
}
.testimoniales__heading {
  text-align: center;
}
.testimoniales__grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 2rem;
}
@media (max-width: 480px) {
  .testimoniales__grid {
    grid-template-columns: 1fr;
  }
}
.testimoniales__elemento {
  background-color: #455a64;
  padding: 4rem;
  border-radius: 2rem;
}
@media (min-width: 1024px) {
  .testimoniales__elemento:nth-child(1) {
    grid-column: 1/4;
    grid-row: 1/2;
  }
  .testimoniales__elemento:nth-child(2) {
    grid-column: 4/6;
    grid-row: 1/2;
  }
  .testimoniales__elemento:nth-child(3) {
    grid-column: 6/7;
    grid-row: 1/3;
  }
  .testimoniales__elemento:nth-child(4) {
    grid-column: 1/3;
  }
  .testimoniales__elemento:nth-child(5) {
    grid-column: 3/6;
  }
}
.testimoniales__persona {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 2rem;
  align-items: center;
}
.testimoniales__persona img {
  border-radius: 100px;
}
.testimoniales__nombre {
  color: #FFFFFF;
  font-weight: 700;
}
.testimoniales__comentario p {
  color: #FFFFFF;
}

.favoritos {
  background-color: #eceeef;
  padding-top: 5rem;
  margin-top: 10rem;
  padding-bottom: 5rem;
}
.favoritos__heading {
  text-align: center;
  color: #455a64;
}
.favoritos__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
}
@media (max-width: 480px) {
  .favoritos__grid {
    grid-template-columns: 1fr;
  }
}
.favoritos__elemento {
  padding: 2rem;
  background-color: #FFFFFF;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 3rem;
  align-items: center;
  box-shadow: 0px 0px 2.6rem -0.8rem;
}
@media (max-width: 480px) {
  .favoritos__elemento {
    grid-template-columns: 1fr;
    text-align: center;
  }
}
.favoritos__elemento img {
  margin-bottom: 2rem;
}
.favoritos__titulo {
  margin: 0;
  color: #e8505b;
}
.favoritos__rank {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.favoritos__rank img {
  width: 70%;
}

.repartidores {
  padding-top: 5rem;
}
.repartidores__heading {
  text-align: center;
  color: #455a64;
}
.repartidores__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
  align-items: center;
}
@media (max-width: 480px) {
  .repartidores__grid {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.repartidores__texto {
  color: #455a64;
  line-height: 1.6;
}
@media (max-width: 480px) {
  .repartidores__texto {
    text-align: center;
  }
}
.repartidores__boton {
  background-color: #e8505b;
  color: #FFFFFF;
  padding: 1rem 2rem;
  border-radius: 1rem;
}
@media (max-width: 480px) {
  .repartidores__boton {
    display: block;
    text-align: center;
  }
}

.descarga {
  margin-top: 10rem;
  padding-top: 10rem;
  background-color: #eceeef;
}
.descarga__contenido {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
  align-items: center;
  text-align: center;
}
@media (max-width: 480px) {
  .descarga__contenido {
    grid-template-columns: 1fr;
  }
}
.descarga__heading {
  color: #455a64;
}
.descarga__elementos {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
.descarga__elemento {
  transform: scale(1.2) rotate(-2dg);
}

.footer {
  margin-top: 5rem;
}
.footer--gris {
  background-color: #455a64;
  padding-top: 5rem;
}
.footer__contenido {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
@media (max-width: 480px) {
  .footer__contenido {
    grid-template-columns: 1fr;
    text-align: center;
  }
}
.footer__elemento {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.footer__heading {
  color: #FFFFFF;
}
.footer__texto {
  color: #FFFFFF;
  line-height: 1.8;
}
.footer__enlace {
  color: #FFFFFF;
}
.footer--rojo {
  background-color: #e8505b;
}
.footer__derechos {
  color: #FFFFFF;
  text-align: center;
  padding: 2rem;
  margin: 0;
}